export default {
  props: {
    watch: {
      type: [String, Array, Boolean],
      default: false
    }
  },
  mounted () {
    let targetIDs = []
    if (this.watch !== false) {
      if (this.watch === true) {
        let watchedElements = [...this.$el.querySelectorAll('[complete]')]
        for (var h = 0; h < watchedElements.length; h++) {
          this.watchArray.push(Number(watchedElements[h].getAttribute('complete')))
        }
      }
      else {
        if (Array.isArray(this.watch)) {
          targetIDs = this.watch
        } else {
          targetIDs.push(this.watch)
        }
        for (var i = 0; i < targetIDs.length; i++) {
          this.watchArray.push(Number(document.getElementById(targetIDs[i]).getAttribute('complete')))
        }
      }
    }
  },
  data () {
    return {
      watchArray: []
    }
  },
  methods: {
    watched () {
      let count = 0
      for (let i = 0; i < this.watchArray.length; i++) {
        count += this.$store.getters.getCompletionItem(this.watchArray[i])
      }
      return count / this.watchArray.length
    }
  },
  computed: {
    isWatched () {
      return this.watched()
    }
  }
}